/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #2D2C2E;
  user-select: none;
}
body.fijar-body {
  overflow: hidden;
}

p {
  color: #000000;
  font-size: 2rem;
}

.texto {
  color: #FAF5E6;
  margin: 0;
}

.resaltado {
  color: #FD1F4A;
}

.punto {
  font-size: 2rem;
  font-weight: bold;
  margin: 0;
  color: #FD1F4A;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.contenedoralt {
  width: 100%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Poppins", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.btnup {
  width: 80px;
  display: flex;
  justify-content: space-between;
  padding: 0.8rem 1rem;
  border-radius: 1.8rem;
  background-color: #FD1F4A;
  border: none;
  position: fixed;
  right: -70px;
  bottom: 3rem;
  transition: all 0.4s ease-in-out;
}
.btnup img {
  width: 3rem;
  margin-left: -0.5rem;
}

.btnup.activ {
  right: -20px;
}

.bground {
  background-color: #2D2C2E;
}

.whatsAppBtn {
  width: 5.5rem;
  position: fixed;
  bottom: 3rem;
  left: -7rem;
  background-color: transparent;
  border: none;
  transition: all 0.5s ease-in-out;
}

.whatsAppBtn.act {
  left: 2rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
  padding: 5rem 2rem;
}

.contenido-centrado {
  max-width: 800px;
}

.header {
  background-color: #333333;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
  min-height: 70vh;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 50rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenido-header {
    min-height: 50rem;
  }
}

.contenido-header {
  display: flex;
  flex-direction: column;
}
.contenido-header h1 {
  text-align: center;
  color: #FAF5E6;
  font-weight: 700;
  font-size: 4rem;
  line-height: 1.4;
  max-width: 60rem;
  margin: 8rem 1rem 2rem 1rem;
}
@media (min-width: 768px) {
  .contenido-header h1 {
    text-align: left;
  }
}
.contenido-header .contenedor-cta {
  padding-bottom: 5rem;
}
@media (min-width: 768px) {
  .contenido-header .contenedor-cta {
    background-color: transparent;
  }
}
.contenido-header .cta {
  display: block;
  width: 90%;
  padding: 0.8rem 2rem;
  margin: 4rem auto 0 auto;
  font-size: 2rem;
  font-family: inherit;
  border-radius: 1rem;
  text-align: center;
  text-decoration: none;
  color: #FAF5E6;
  background-image: linear-gradient(to top, #FD1F4A 0%, #c0042a 100%);
}
@media (min-width: 768px) {
  .contenido-header .cta {
    margin: 4rem 0;
    width: 50%;
    padding: 0.5rem 2rem;
  }
}
.contenido-header .contenedor-hero {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .contenido-header .contenedor-hero {
    flex-direction: row;
    align-items: center;
  }
}
.contenido-header .mariachi-movil {
  display: block;
  width: 40%;
  margin: 12rem auto 0 auto;
}
@media (min-width: 768px) {
  .contenido-header .mariachi-movil {
    display: none;
  }
}
.contenido-header .mariachi {
  display: none;
  width: 50%;
  margin: 2rem auto;
  filter: drop-shadow(3px 3px 5px #2D2C2E);
}
@media (min-width: 768px) {
  .contenido-header .mariachi {
    display: block;
    width: 20%;
  }
}

.title {
  color: #FAF5E6;
}

.footer {
  padding: 1rem;
  background-color: #2D2C2E;
}
.footer .contenido-footer {
  padding-top: 5rem;
}
.footer .copyright {
  color: #FAF5E6;
  text-align: center;
  margin: 0;
}

.barra {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  padding: 1rem 2rem;
  z-index: 1;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    position: initial;
    padding: 0;
  }
}

.contenedor-logo {
  display: flex;
  z-index: 10;
}
.contenedor-logo img {
  width: 4rem;
  margin: 0 0.5rem 0 0;
}
.contenedor-logo span {
  font-weight: 200;
}
.contenedor-logo h3 {
  color: #FAF5E6;
  font-weight: 900;
  font-size: 3rem;
  margin: 0;
}

.navegacion {
  background-color: #2D2C2E;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: auto;
  margin: 0;
  padding: 10rem 0 5rem 0;
  transition: all 0.3s ease-in-out;
  z-index: 1;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    flex-direction: row;
    position: initial;
    background-color: transparent;
    padding: 0;
    width: auto;
    height: auto;
  }
}
.navegacion a {
  color: #FAF5E6;
  text-decoration: none;
  display: block;
  font-size: 2.2rem;
  text-align: left;
  transition: all 0.3s;
  width: 100%;
  padding: 2rem 0 2rem 2rem;
}
.navegacion a:hover {
  color: #FD1F4A;
}
@media (min-width: 768px) {
  .navegacion a {
    margin-right: 1.5rem;
  }
  .navegacion a:last-of-type {
    margin-right: 0;
  }
}

.navegacion.active {
  left: 0;
}

.ham {
  outline: none;
  box-shadow: none;
  cursor: pointer;
  position: absolute;
  top: 2.2rem;
  right: 1rem;
  border: none;
  margin: 0;
  padding: 0;
  background-color: transparent;
  z-index: 10;
  transition: all 0.3s ease-in-out;
}
@media (min-width: 768px) {
  .ham {
    display: none;
  }
}
.ham span {
  outline: none;
  display: block;
  background-color: #FAF5E6;
  height: 3px;
  width: 28px;
  margin: 5px auto;
  border-radius: 2px;
  transition: all 0.3s ease-in-out;
}
.ham .barra1.animado {
  transform: rotate(-45deg) translate(-7px, 4px);
}
.ham .barra2.animado {
  opacity: 0;
}
.ham .barra3.animado {
  transform: rotate(45deg) translate(-7px, -4px);
}

.ham.activ {
  transform: rotate(180deg);
}

.ajustado {
  padding: 5rem 2rem;
  background-color: #333333;
}
.ajustado .title-seccion {
  font-size: 4rem;
  margin: 0 0 3rem 0;
  color: #FAF5E6;
}
.ajustado h2 {
  color: #FD1F4A;
  margin: 0;
}
.ajustado .contenedor-cards {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .ajustado .contenedor-cards {
    flex-direction: row;
    align-items: flex-start;
    justify-content: center;
    gap: 4rem;
  }
}
.ajustado .card {
  background-color: #2D2C2E;
  margin: 1rem 0;
  padding: 1rem 3rem 3rem 3rem;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .ajustado .card {
    width: 480px;
  }
}
.ajustado .card img {
  width: 40%;
  display: block;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .ajustado .card img {
    width: 20%;
  }
}
.ajustado .precio {
  color: #FAF5E6;
  font-size: 3.2rem;
  font-weight: 500;
  margin: 0 0 2rem 0;
}
.ajustado .list {
  color: #FAF5E6;
}
.ajustado .list li {
  font-size: 2rem;
}

.interna {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .interna {
    padding: 5rem;
  }
}

.contenedor-seccion {
  color: #FAF5E6;
}
.contenedor-seccion h2 {
  color: #faf5e6;
  font-size: 4rem;
  margin: 0 0 3rem;
}
@media (min-width: 768px) {
  .contenedor-seccion h2 {
    margin-bottom: 5rem;
  }
}
.contenedor-seccion p {
  color: #FAF5E6;
  padding: 2rem;
}
@media (min-width: 768px) {
  .contenedor-seccion p {
    padding: 0 10rem;
  }
}
@media (min-width: 768px) {
  .contenedor-seccion img {
    width: 30%;
  }
}
.contenedor-seccion .contenedor-interior {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  background-color: #333333;
}
@media (min-width: 768px) {
  .contenedor-seccion .contenedor-interior {
    flex-direction: row;
    width: 80%;
    margin: 0 auto;
  }
}

.galeria {
  padding: 5rem 0;
}
.galeria h2 {
  text-align: center;
  font-size: 4rem;
  margin: 0 0 3rem 0;
  color: #FAF5E6;
}
.galeria p {
  text-align: center;
  margin-bottom: 5rem;
}

.galeria-imagenes {
  list-style: none;
  margin: 2rem 0;
  padding: 0;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
}
@media (min-width: 480px) {
  .galeria-imagenes {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
@media (min-width: 768px) {
  .galeria-imagenes {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 1024px) {
  .galeria-imagenes {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(6, 1fr);
  }
}
.galeria-imagenes img {
  border-radius: 10px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}
.galeria-imagenes img:hover {
  filter: brightness(50%);
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.7882352941);
  z-index: 100;
}
.overlay picture {
  display: block;
  max-width: 60rem;
  border-radius: 2rem;
  width: 90%;
  margin: 5rem auto;
}

.cerrar {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: #FD1F4A;
  color: #FAF5E6;
  display: grid;
  place-items: center;
  font-weight: 700;
  margin-top: -18rem;
}
.cerrar:hover {
  cursor: pointer;
}

.contacto {
  background-color: #333333;
  padding: 5rem 0;
}
.contacto h2 {
  text-align: center;
  font-size: 4rem;
  margin: 0 0 3rem 0;
  color: #FAF5E6;
}
.contacto p {
  text-align: center;
  color: #FAF5E6;
}

.contenedor-contacto {
  background-color: #2D2C2E;
  margin: 3rem 1rem;
  padding: 1rem 3rem 3rem 3rem;
  border-radius: 1rem;
}
.contenedor-contacto a {
  display: block;
  width: 90%;
  margin: 2rem auto;
  padding: 1rem 2rem;
  border-radius: 1rem;
  color: #FAF5E6;
  background-color: #FD1F4A;
  text-align: center;
}
@media (min-width: 768px) {
  .contenedor-contacto a {
    width: 30%;
  }
}

.recomendaciones {
  padding: 5rem 1rem;
  color: #FAF5E6;
}
.recomendaciones h2 {
  color: #faf5e6;
  font-size: 4rem;
  margin: 0 0 3rem;
  text-align: center;
}

.card-contenedor {
  display: flex;
  flex-wrap: nowrap;
  overflow: scroll;
}

.card-cometario {
  position: relative;
  flex: 1 0 auto;
  margin: 2rem;
  background-color: #333333;
  border-radius: 1.5rem;
  width: 230px;
  height: auto;
  text-align: center;
  padding: 3rem 2rem;
}
.card-cometario p {
  color: #FAF5E6;
}
.card-cometario small {
  position: absolute;
  left: 50%;
  transform: translate(-50%);
  bottom: 2rem;
  color: #FD1F4A;
  margin: 0;
  width: 100%;
  font-size: 1.7rem;
}

.comillas {
  width: 70px;
}